<template>
  <div class="option-switch w-fill">
    
    <div v-if="colorBtn" class="flex flex-wrap w-fill jc-between my-btn">
      <m-button
        v-for="(item, index) in tabs"
        @click="handleClick(item, index)"
        :key="item.id"
        height="41"
        :class="
          activeIndex === index ? `${item.border}` : 'order-gray hdr-el-gray filter-gray'
        "
        class="bet-btn border-gray option-switch-typevalues flex1 option-switch-tab"
      >
        <div class="flex jc-center ai-center fs-18">
          <img
            style="padding-right:3px !important;"
            v-show="item.image"
            class="option-switch-image"
            :src="item.image"
            alt=""
          />        
          <span :class="item.color" class="text fs-16">{{ language [item.lable]  }}</span>
        </div>
      </m-button>
    </div>

    <div v-else class="flex flex-wrap w-fill jc-between">
      <m-input-plane
        v-for="(item, index) in tabs"
        :key="item.id"
        :active="activeIndex === index"
        class="option-switch-typevalues flex1 option-switch-tab"
        @click="handleClick(item, index)"
      >
        <div class="option-switch-switch flex jc-center ai-center fs-18">
          <img v-show="item.image" class="option-switch-image" :src="item.image" alt="">
          <span :class="item.color" class="text fs-16">{{item.lable}}</span>
        </div>
      </m-input-plane>
    </div>

  </div>
</template>

<script>
import { mapGetters ,mapState} from "vuex";
import mInputPlane from "@/components/mInputPlane";
import MButton from "@/components/mButton";
import icon_bnb from "@/images/option/BNB.png";
import icon_eth from "@/images/coins/ETH.svg";
export default {
  name: "OptionSwitch",
  components: { mInputPlane, MButton },
  props: {
    title: { type: String, default: "" },
    colorBtn: { type: Boolean },
    tabs: { type: Array },
  },
  computed: {
      ...mapState('option',['currentPrice']),
    ...mapGetters(["language"]),
  },
  data() {
    return {
      activeIndex: 0,
      listShow: false,
      coinPairs: [
        {symbol:'BNBUSDT',display: 'BNB/USDT', contractName: 'BnbPricePrediction', priceContractName: 'AggregatorV3InterfaceBnb', icon: icon_bnb},
        {symbol:'ETHUSDT',display: 'ETH/USDT', contractName: 'EthPricePrediction', priceContractName: 'AggregatorV3InterfaceEth', icon: icon_eth},
      ],
    };
  },
  methods: {
    handleClick(item, index) {
      if (index === this.activeIndex) return;
      this.activeIndex = index;
      this.$emit("onChange", item);
    },
  },
};
</script>

