  position: relative;
 
  .b-icon {
    height: 38px;
    margin-right: 10px;
  }
  .display {
    font-size: 13px;
  }
  .price {
    font-size: 16px;
  }
  .arrow {
    width: 30px;
    &.show {
      transform: rotate(180deg);
    }
  }
}
.pair-list {
  width: 130px;
  position: absolute;
  top: 70px;
  left: 10px;
  overflow: hidden;
  border-radius: 10px;
  border: 2px solid #FFFFFF;
  z-index: 99;
  background-color: #191f2d;
  transform: translateY(-100%-30px);
  &.show {
    transform: translateY(0);
  }
  .logo{
    display: none;
  }
  .pair {
    &:hover {
      background-color: #242e40;
    }
    &.active {
      background-color: #323b55;
    }
  }
}
.mask{
  z-index: 9;
  display: none;
}

@media screen and (max-width: 768px) {
   .mask{
    display: block;
  }
  .pair-list{
    width: 50%;
    max-width: 260px;
    height: 100%;
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    left: 0;
    border: none;
    border-radius: 0;
    z-index: 999999999;
    box-shadow: none;
    .logo{
      display: block;
      img{
        width: 100%;
      }
    }
    &.show {
      transform: translateX(0);
    }
  }
}



