<template>
  <div class="w-fill">
    <div class="fs-18 mt-16" style="margin-bottom:20px">{{title}}</div>
    <div class="flex flex-wrap">
      <!-- <m-input-plane class="mt-16  flex1 flex ai-center my-int"> -->
        <m-currency-input :decimals="18" class="border" v-model="currentValue" />
        <span>{{symbol}}</span>
      <!-- </m-input-plane> -->
    </div>

    <!-- <div class="tips-color mt-16">
       {{language.wallet_balance}} 
       <span @click="hanldeInpputBalance" class="cursor-p">{{balance}}</span>
       {{symbol1 || symbol}}
    </div> -->

  </div>
</template>

<script>
import {mapGetters} from "vuex";
import mCurrencyInput from "@/components/mCurrencyInput";

function equal(value1, value2) {
  return String(value1) === String(value2);
} // add num and avoid float number

export default {
  name: "OptionInputAmount",
  components: {mCurrencyInput},
  props: {
    modelValue: {type:[Number, String]},
    title: {type: String, default: ''},
    symbol: {type: String, default: ''},
    symbol1: {type: String, default: ''},
    balance: {type: String, default: ''},
    disableBalace: {type: Boolean},
    maxBalance:{type: String}
  },
  computed:{
    ...mapGetters(['language']),
  },
  data(){
    return {
      currentValue:'',
      timer: null
    }
  },
  watch: {
    modelValue: function (value) {
      if (!equal(value, this.currentValue)) {
        this.currentValue = value;
      }
      if(this.maxBalance < value){
            this.currentValue = this.maxBalance;
      }
    },
     maxBalance(){
        return this.maxBalance
     },
    currentValue: function(value) {
      if (!equal(value, this.modelValue)) {
        this.$emit('update:modelValue', value)
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$emit('change', value);
        }, 300)
      }
    }
  },
  mounted() {
    this.currentValue = this.modelValue;
  },
  methods: {
    hanldeInpputBalance() {
      if (this.disableBalace) return
      this.currentValue = this.balance
    },
  },
}
</script>

