<template>
  <div class="m-slider-contener">
    <div @click="clickHandle" class="m-slider">
      <div class="m-slider-rail" ref="rail">
        <div class="m-slider-process"
          :class="{
            shadow,
            move: isMoveing,
          }"
          :style="{
            width: leftRate,
          }"
        ></div>
        <div class="m-slider-dot" v-if="status"
          :class="{
            move: isMoveing
          }"
          :style="{
            left: leftRate,
            background: 'red'
          }">
          <div
            
            @mousedown="dragStart"
            @touchstart="dragStart"
            class="m-slider-dot-handle"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { State } from '@/common/util'

const SliderState = {
  None: 0,
  Drag: 1 << 1,
  Focus: 1 << 2,
}

const getOffset = (elem) => {
  const doc = document.documentElement
  const body = document.body
  const rect = elem.getBoundingClientRect()
  const offset = {
    y: rect.top + (window.pageYOffset || doc.scrollTop) - (doc.clientTop || body.clientTop || 0),
    x:
      rect.left + (window.pageXOffset || doc.scrollLeft) - (doc.clientLeft || body.clientLeft || 0),
  }
  return offset
}

const getPos = (
  e,
  elem,
  isReverse,
) => {
  const event = 'targetTouches' in e ? e.targetTouches[0] : e
  const offset = getOffset(elem)
  const posObj = {
    x: event.pageX - offset.x,
    y: event.pageY - offset.y,
  }
  return {
    x: isReverse ? elem.offsetWidth - posObj.x : posObj.x,
    y: isReverse ? elem.offsetHeight - posObj.y : posObj.y,
  }
}


export default {
  name: "mSlider",

  props: {
    modelValue: {type: Number},
    shadow: {type: Boolean},
    disabled: {type: Boolean},
    min: {type: Number, default: 0},
    max: {type: Number, default: 100},
    status:{type: Boolean,default: true},
  },

  data() {
    return {
      states: new State(SliderState),
      pos: 0,
      rateVal: 0,
      leftRate: '0',
      realValue: 0,
      timer: null,
    }
  },
  mounted() {
    this.setValueByModelValue(this.modelValue)
    // if (this.modelValue !== this.realValue) {
    //   }
    if (!this.disabled) {
      this.bindEvent()
    }
  },

  beforeUnmount() {
    if (!this.disabled) {
      this.unbindEvent()
    }
  },

  computed: {
    isMoveing() {
      return this.states.has(SliderState.Drag)
    },
    range() {
      return this.max - this.min
    }
  },

  watch: {
    modelValue(value) {
      if (value !== this.realValue) {
        this.setValueByModelValue(value)
      }
    },
  },

  methods: {
    onHandleChange(value) {
      if (value !== this.modelValue) {
        this.$emit('update:modelValue', value);
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$emit('change', value);
        }, 300)
      }
    },

    bindEvent() {
      document.addEventListener('touchmove', this.dragMove, { passive: false })
      document.addEventListener('touchend', this.dragEnd, { passive: false })
      document.addEventListener('mousedown', this.blurHandle)
      document.addEventListener('mousemove', this.dragMove, { passive: false })
      document.addEventListener('mouseup', this.dragEnd)
      document.addEventListener('mouseleave', this.dragEnd)
    },
  
    unbindEvent() {
      document.removeEventListener('touchmove', this.dragMove)
      document.removeEventListener('touchend', this.dragEnd)
      document.removeEventListener('mousemove', this.dragMove)
      document.removeEventListener('mouseup', this.dragEnd)
      document.removeEventListener('mouseleave', this.dragEnd)
    },

    dragMove(e) {
      if (!this.states.has(SliderState.Drag)) {
        return false
      }
      e.preventDefault()
      const pos = this.getPosByEvent(e)
      this.setValueByPos(pos)
    },

    dragEnd(e) {
      if (!this.states.has(SliderState.Drag)) {
        return false
      }

      setTimeout(() => {
        this.states.delete(SliderState.Drag)
        // If useKeyboard is true, keep focus status after dragging
        if ('targetTouches' in e) {
          this.states.delete(SliderState.Focus)
        }
      })
    },

    getPosByEvent(e) {
      return getPos(e, this.$refs.rail, this.isReverse)['x']
    },

    getRateVal(pos) {
      if (pos <= 0) return 0
      if (pos >= this.$refs.rail.offsetWidth) return 1
      return pos / this.$refs.rail.offsetWidth
    },

    dragStart() {
      if (this.disabled) return
      this.states.add(SliderState.Drag)
      this.states.add(SliderState.Focus)
    },

    clickHandle(e) {
      if (this.disabled) return
      if (this.states.has(SliderState.Drag)) {
        return
      }
      const pos = this.getPosByEvent(e)
      this.setValueByPos(pos)
    },

    setValueByPos(pos) {
      this.pos = pos
      const rateVal = this.getRateVal(pos)
      this.leftRate = `${(rateVal * 100).toFixed(2)}%`
      this.rateVal = rateVal
      const value = Number.parseInt(this.range * rateVal) + this.min
      this.realValue = value
      this.onHandleChange(value)
    },

    setValueByModelValue (value) {
      console.log(value, 'setValueByModelValue')
      const rateVal = (value - this.min) / this.range
      // this.pos = pos
      this.leftRate = `${(rateVal * 100).toFixed(2)}%`
      this.rateVal = rateVal
      this.realValue = value
    },

  }

}
</script>

<style lang="less" scoped>
.m-slider-contener{
  width: auto;
  // padding-left: 14px;
  // padding-right: 14px;
}
.m-slider{
  position: relative;
  user-select: none;
  width: auto;
  height: 8px;
  &-rail{
    width: 100%;
    height: 100%;
    background: #353B4D;
    border-radius: 4px;
  }
  &-process{
    
    border-radius: 15px;
    transition: all 0.5s ease;
    
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 50%;
    &.shadow{
      background: #FFFFFF;
      box-shadow: 0 0 10px 2px rgba(0, 225, 255, 0.6), 0 0 10px -2px rgba(0, 225, 255, 0.6), 0 0 2px 0px rgba(0, 225, 255, 0.6) inset, 0 0 2px 0px rgba(0, 225, 255, 0.6);
    }
    &.move{
      transition: width 0s ease;
      &.shadow{
        box-shadow: 0 0 15px 2px rgba(0, 225, 255, 0.6), 0 0 15px -2px rgba(0, 225, 255, 0.6), 0 0 2px 0px rgba(0, 225, 255, 0.6) inset, 0 0 2px 0px rgba(0, 225, 255, 0.6);
      }
    }
  }
  &-dot{
    position: absolute;
    z-index: 5;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    transition: 0.5s ease;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    &.move{
      transition: 0s ease;
    }
    &-handle{
      cursor: pointer;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: #26CE81;
      border: 2px solid #FFFFFF;
      box-sizing: border-box;
      box-shadow: 0px 0px 10px 2px #26CE81;
      transition: 0.5s ease;
      &:hover{
        box-shadow: 0px 0px 15px 2px #26CE81;
      }
    }
  }
}
</style>