<template>
  <input
    class="m-currency-input"
    :value="modelValue"
    ref="inputDom"
    inputMode="decimal"
    :pattern="`^[0-9]*[.,]?[0-9]{0,${decimals}}$`"
    :placeholder="placeholder || '0.0'"
    @input="onInput"
    type="text">
</template>

<script>
export default {
  name: "mCurrencyInput",
  props: {
    modelValue: {type:[Number, String]},
    decimals: {type: Number, default: 4},
    placeholder: {type: String, default: ''},
  },
   methods: {
    onInput(event) {
      if (event.target.validity.valid) {
        this.$emit('update:modelValue', event.target.value);
      } else {
        this.$refs.inputDom.value = this.modelValue
      }
    },
  },
}
</script>

<style lang="less" scoped>
.m-currency-input{
  width: 100%;
  min-width: 24px;
}
</style>